<template>
  <div class="sets-filter">
    <div class="sets-header">
      <h3>Pokmon Sets</h3>
      <button class="clear-filter" @click="$emit('clear')" v-if="selectedSet">
        <span class="material-symbols-rounded">close</span>
        Clear
      </button>
    </div>
    
    <div class="sets-grid">
      <div
        v-for="set in sets"
        :key="set.id"
        class="set-item"
        :class="{ active: selectedSet === set.id }"
        @click="$emit('select', set.id)"
      >
        <img :src="set.images.symbol" :alt="set.name" class="set-symbol">
        <div class="set-info">
          <span class="set-name">{{ set.name }}</span>
          <span class="set-date">{{ formatDate(set.releaseDate) }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    sets: {
      type: Array,
      required: true
    },
    selectedSet: {
      type: String,
      default: null
    }
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleDateString('en-US', {
        month: 'short',
        year: 'numeric'
      });
    }
  }
}
</script>

<style scoped>
.sets-filter {
  background: var(--card-background);
  border-radius: 16px;
  padding: 16px;
  margin-bottom: 24px;
}

.sets-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.sets-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 12px;
  max-height: 200px;
  overflow-y: auto;
  padding-right: 8px;
}

.set-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 1px solid var(--card-border);
}

.set-item:hover {
  background: var(--surface-background);
}

.set-item.active {
  background: var(--accent-primary);
  border-color: var(--accent-primary);
}

.set-symbol {
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.set-info {
  display: flex;
  flex-direction: column;
}

.set-name {
  font-size: 13px;
  font-weight: 500;
}

.set-date {
  font-size: 11px;
  color: var(--text-secondary);
}

.clear-filter {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 4px 8px;
  border-radius: 6px;
  border: none;
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 12px;
  cursor: pointer;
}
</style>
