<template>
  <div 
    class="cart-overlay"
    :class="{ 'is-open': cart.state.isOpen }"
    @click="cart.close()"
  >
    <div 
      class="cart-panel"
      @click.stop
      :class="{ 'is-open': cart.state.isOpen }"
    >
      <div class="cart-header">
        <h2>Shopping Cart</h2>
        <button class="close-button" @click="cart.close()">
          <span class="material-symbols-rounded">close</span>
        </button>
      </div>

      <div class="cart-items">
        <TransitionGroup name="cart-item">
          <div 
            v-for="item in cart.state.items" 
            :key="item.id"
            class="cart-item"
          >
            <img :src="item.image_url" :alt="item.name">
            <div class="item-info">
              <h3>{{ item.name }}</h3>
              <p class="price">${{ item.price }}</p>
              <div class="quantity-controls">
                <button 
                  @click="updateQuantity(item.id, item.quantity - 1)"
                  :disabled="item.quantity <= 1"
                >-</button>
                <span>{{ item.quantity }}</span>
                <button 
                  @click="updateQuantity(item.id, item.quantity + 1)"
                  :disabled="item.quantity >= item.stock"
                >+</button>
              </div>
            </div>
            <button 
              class="remove-button"
              @click="cart.removeItem(item.id)"
            >
              <span class="material-symbols-rounded">delete</span>
            </button>
          </div>
        </TransitionGroup>
      </div>

      <div class="cart-footer" v-if="cart.state.items.length">
        <div class="cart-total">
          <span>Total</span>
          <span class="total-amount">${{ cart.getTotal().toFixed(2) }}</span>
        </div>
        <button class="checkout-button" @click="checkout">
          Proceed to Checkout
        </button>
      </div>

      <div 
        v-else 
        class="empty-cart"
      >
        <span class="material-symbols-rounded">shopping_cart</span>
        <p>Your cart is empty</p>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue'
import cart from '@/store/cart'

export default defineComponent({
  name: 'CartSlideOver',
  setup() {
    function updateQuantity(productId, quantity) {
      if (quantity > 0) {
        cart.updateQuantity(productId, quantity)
      }
    }

    async function checkout() {
      // Implement checkout logic
    }

    return {
      cart,
      updateQuantity,
      checkout
    }
  }
})
</script>

<style scoped>
.cart-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(4px);
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.cart-overlay.is-open {
  opacity: 1;
  visibility: visible;
}

.cart-panel {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  max-width: 400px;
  background: var(--card-background);
  border-left: 1px solid var(--card-border);
  display: flex;
  flex-direction: column;
  transform: translateX(100%);
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.cart-panel.is-open {
  transform: translateX(0);
}

/* Add more styles... */
</style>
