<template>
  <div 
    class="preview-overlay"
    :class="{ 'is-open': isOpen }"
    @click="close"
  >
    <div 
      class="preview-panel"
      @click.stop
      :class="{ 'is-open': isOpen }"
    >
      <button class="close-button" @click="close">
        <span class="material-symbols-rounded">close</span>
      </button>

      <div class="preview-content" v-if="product">
        <div class="preview-image">
          <img :src="product.image_url" :alt="product.name">
        </div>
        
        <div class="preview-info">
          <span class="product-type">{{ product.type }}</span>
          <h2>{{ product.name }}</h2>
          
          <div class="product-meta">
            <div class="meta-item" v-if="product.metadata && product.metadata.set">
              <h4>Set</h4>
              <p>{{ product.metadata.set.name }}</p>
            </div>
            <div class="meta-item" v-if="product.metadata && product.metadata.rarity">
              <h4>Rarity</h4>
              <p>{{ product.metadata.rarity }}</p>
            </div>
          </div>

          <div class="price-section">
            <div class="price">${{ product.price }}</div>
            <div class="stock" :class="getStockLevel(product.stock)">
              {{ product.stock }} in stock
            </div>
          </div>

          <button 
            class="add-to-cart"
            :disabled="product.stock <= 0"
            @click="addToCart"
          >
            Add to Cart
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue'
import cart from '@/store/cart'

export default defineComponent({
  name: 'QuickPreview',
  props: {
    product: Object,
    isOpen: Boolean
  },
  emits: ['close'],
  setup(props, { emit }) {
    function close() {
      emit('close')
    }

    function addToCart() {
      cart.addItem(props.product)
      close()
    }

    function getStockLevel(stock) {
      if (stock <= 0) return 'out'
      if (stock < 5) return 'low'
      return 'good'
    }

    return {
      close,
      addToCart,
      getStockLevel
    }
  }
})
</script>

<style scoped>
.preview-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(8px);
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.preview-overlay.is-open {
  opacity: 1;
  visibility: visible;
}

.preview-panel {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -45%) scale(0.95);
  background: var(--card-background);
  border-radius: 24px;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow: auto;
  padding: 32px;
  opacity: 0;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.preview-panel.is-open {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

.close-button {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  border: none;
  background: var(--surface-background);
  color: var(--text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.close-button:hover {
  background: var(--background-elevated);
  transform: scale(1.1);
}

.preview-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  align-items: start;
}

.preview-image {
  text-align: center;
}

.preview-image img {
  max-width: 100%;
  height: auto;
  border-radius: 16px;
  transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.preview-image img:hover {
  transform: scale(1.05) rotate(2deg);
}

.preview-info {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.product-type {
  font-size: 14px;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.preview-info h2 {
  font-size: 32px;
  font-weight: 700;
  margin: 0;
  color: var(--text-primary);
}

.product-meta {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 16px;
}

.meta-item {
  padding: 16px;
  background: var(--surface-background);
  border-radius: 12px;
  border: 1px solid var(--card-border);
}

.meta-item h4 {
  font-size: 13px;
  color: var(--text-secondary);
  margin: 0 0 4px 0;
}

.meta-item p {
  font-size: 16px;
  color: var(--text-primary);
  margin: 0;
  font-weight: 500;
}

.price-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--surface-background);
  padding: 16px;
  border-radius: 12px;
  border: 1px solid var(--card-border);
}

.price {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
}

.stock {
  font-size: 14px;
  padding: 4px 12px;
  border-radius: 100px;
}

.stock.good {
  background: var(--system-green-alpha);
  color: var(--system-green);
}

.stock.low {
  background: var(--system-yellow-alpha);
  color: var(--system-yellow);
}

.stock.out {
  background: var(--system-red-alpha);
  color: var(--system-red);
}

.add-to-cart {
  width: 100%;
  padding: 16px;
  border: none;
  border-radius: 12px;
  background: var(--accent-primary);
  color: white;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.add-to-cart:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.add-to-cart:disabled {
  background: var(--surface-background);
  color: var (--text-secondary);
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .preview-content {
    grid-template-columns: 1fr;
  }
  
  .preview-panel {
    padding: 24px;
  }
}
</style>
