<template>
  <div class="store">
    <!-- Hero Section -->
    <div class="store-hero">
      <div class="hero-content">
        <h1>Card Market</h1>
        <p class="subtitle">Discover rare and valuable Pokmon cards</p>
      </div>
      <div class="quick-categories">
        <button 
          v-for="category in categories" 
          :key="category.id"
          class="category-pill"
          :class="{ active: filters.category === category.id }"
          @click="filters.category = category.id"
        >
          <span class="material-symbols-rounded">{{ category.icon }}</span>
          {{ category.name }}
        </button>
      </div>
    </div>

    <!-- Search and Filters -->
    <div class="search-container">
      <div class="search-box">
        <span class="material-symbols-rounded">search</span>
        <input 
          v-model="search" 
          type="text" 
          placeholder="Search for cards, sets, or boosters..."
        >
      </div>
      <div class="filter-pills">
        <select v-model="filters.sort" class="filter-select">
          <option value="newest">Latest Arrivals</option>
          <option value="price_low">Price: Low to High</option>
          <option value="price_high">Price: High to Low</option>
          <option value="popularity">Most Popular</option>
        </select>
        <button 
          class="filter-button"
          @click="showFilters = true"
        >
          <span class="material-symbols-rounded">tune</span>
          Filters
        </button>
      </div>
    </div>

    <!-- Product Grid -->
    <div class="products-grid">
      <div v-for="product in filteredProducts" 
           :key="product.id" 
           class="product-card"
           :class="{ 'out-of-stock': product.stock <= 0 }"
      >
        <div class="product-image">
          <img :src="product.image_url" :alt="product.name">
          <div class="product-badges" v-if="product.metadata">
            <span v-if="product.metadata.rarity" class="rarity-badge">
              {{ product.metadata.rarity }}
            </span>
            <span v-if="product.stock < 5 && product.stock > 0" class="stock-badge">
              Low Stock
            </span>
          </div>
        </div>
        <div class="product-info">
          <div class="product-header">
            <span class="product-type">{{ product.type }}</span>
            <h3>{{ product.name }}</h3>
          </div>
          <div class="product-meta">
            <span class="product-set" v-if="product.metadata && product.metadata.set">
              {{ product.metadata.set.name }}
            </span>
            <span class="product-price">${{ product.price }}</span>
          </div>
          <button 
            class="buy-button"
            :disabled="product.stock <= 0"
            @click="purchaseProduct(product)"
          >
            {{ product.stock <= 0 ? 'Out of Stock' : 'Add to Cart' }}
          </button>
        </div>
      </div>
    </div>

    <!-- Filters Modal -->
    <Modal v-if="showFilters" @close="showFilters = false">
      <template #header>
        <h2>Filter Products</h2>
      </template>
      <template #default>
        <div class="filters-form">
          <!-- ... existing filter form ... -->
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { supabase } from '@/services/supabase'
import Modal from '@/components/Modal.vue'
import auth from '@/store/auth'
import CartSlideOver from '@/components/CartSlideOver.vue'
import QuickPreview from '@/components/QuickPreview.vue'
import cart from '@/store/cart'

export default {
  name: 'Store',
  components: { 
    Modal, 
    CartSlideOver,
    QuickPreview
  },
  setup() {
    const search = ref('')
    const showSellModal = ref(false)
    const showBuyModal = ref(false)
    const showFilters = ref(false)
    const selectedListing = ref(null)
    const listings = ref([])
    const filters = ref({
      condition: '',
      sort: 'newest',
      category: ''
    })
    const categories = ref([
      { id: 'all', name: 'All', icon: 'category' },
      { id: 'rare', name: 'Rare', icon: 'star' },
      { id: 'common', name: 'Common', icon: 'circle' },
      { id: 'uncommon', name: 'Uncommon', icon: 'diamond' }
    ])

    // Create computed properties
    const currentUserId = computed(() => {
      return auth.state.user ? auth.state.user.id : null
    })

    const filteredProducts = computed(() => {
      let filtered = [...listings.value]

      // Apply search filter
      if (search.value) {
        const searchTerm = search.value.toLowerCase()
        filtered = filtered.filter(product => 
          product.name.toLowerCase().includes(searchTerm)
        )
      }

      // Apply category filter
      if (filters.value.category && filters.value.category !== 'all') {
        filtered = filtered.filter(product => 
          product.category === filters.value.category
        )
      }

      // Apply sort
      switch (filters.value.sort) {
        case 'price_low':
          filtered.sort((a, b) => a.price - b.price)
          break
        case 'price_high':
          filtered.sort((a, b) => b.price - a.price)
          break
        case 'newest':
          filtered.sort((a, b) => new Date(b.created_at) - new Date(a.created_at))
          break
        case 'popularity':
          filtered.sort((a, b) => b.popularity - a.popularity)
          break
      }

      return filtered
    })

    // Fetch listings
    async function fetchListings() {
      const { data, error } = await supabase
        .from('products')
        .select(`*`)
        .eq('active', true)

      if (error) {
        console.error('Error fetching listings:', error)
        return
      }

      listings.value = data || []
    }

    function formatCondition(condition) {
      return condition.replace('_', ' ').replace(/\b\w/g, l => l.toUpperCase())
    }

    function buyCard(listing) {
      selectedListing.value = listing
      showBuyModal.value = true
    }

    async function confirmPurchase() {
      if (!selectedListing.value || !auth.state.user) return

      try {
        // Create order
        const { error: orderError } = await supabase
          .from('orders')
          .insert({
            product_id: selectedListing.value.id,
            user_id: auth.state.user.id,
            quantity: 1,
            total_amount: selectedListing.value.price,
            status: 'completed'
          })

        if (orderError) throw orderError

        // Update product status and stock
        const { error: productError } = await supabase
          .from('products')
          .update({ 
            active: false,
            stock: selectedListing.value.stock - 1 
          })
          .eq('id', selectedListing.value.id)

        if (productError) throw productError

        showBuyModal.value = false
        await fetchListings()
      } catch (error) {
        console.error('Error processing purchase:', error)
      }
    }

    const selectedProduct = ref(null)
    const showPreview = ref(false)

    function showQuickPreview(product) {
      selectedProduct.value = product
      showPreview.value = true
    }

    // Initialize
    fetchListings()

    return {
      search,
      filters,
      filteredProducts,
      showSellModal,
      showBuyModal,
      showFilters,
      selectedListing,
      buyCard,
      confirmPurchase,
      formatCondition,
      currentUserId,
      categories,
      selectedProduct,
      showPreview,
      showQuickPreview,
      cart
    }
  }
}
</script>

<style scoped>
.store {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 24px;
}

.store-hero {
  padding: 64px 0 32px;
  text-align: center;
}

.hero-content h1 {
  font-size: 48px;
  font-weight: 700;
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0;
}

.subtitle {
  font-size: 18px;
  color: var(--text-secondary);
  margin: 12px 0 32px;
}

.quick-categories {
  display: flex;
  gap: 12px;
  justify-content: center;
  flex-wrap: wrap;
}

.category-pill {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  border-radius: 100px;
  border: none;
  background: var(--surface-background);
  color: var(--text-secondary);
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.category-pill:hover {
  background: var(--background-elevated);
  transform: translateY(-1px);
}

.category-pill.active {
  background: var(--accent-primary);
  color: white;
}

.search-container {
  position: sticky;
  top: 80px;
  z-index: 90;
  background: var(--background-primary);
  padding: 24px 0;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 1px solid var(--card-border);
  margin-bottom: 32px;
}

.search-box {
  background: var(--surface-background);
  border-radius: 16px;
  padding: 16px;
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
  border: 1px solid var(--card-border);
  transition: all 0.3s ease;
}

.search-box:focus-within {
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 4px var(--accent-primary-alpha);
}

.search-box input {
  width: 100%;
  border: none;
  background: none;
  font-size: 16px;
  color: var(--text-primary);
  outline: none;
}

.filter-pills {
  display: flex;
  gap: 12px;
}

.filter-select {
  padding: 10px 16px;
  border-radius: 12px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 14px;
  cursor: pointer;
  min-width: 180px;
  appearance: none;
}

.filter-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  border-radius: 12px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 14px;
  cursor: pointer;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 24px;
  padding-bottom: 64px;
}

.product-card {
  background: var(--card-background);
  border-radius: 20px;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--card-border);
  position: relative;
  isolation: isolate;
  transform-origin: center;
  will-change: transform;
}

.product-card::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 20px;
  background: linear-gradient(
    135deg,
    var(--accent-primary-alpha),
    transparent
  );
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.product-card:hover {
  animation: cardHover 0.6s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.product-card:hover::before {
  opacity: 0.1;
}

.product-image {
  position: relative;
  aspect-ratio: 1;
  background: var(--surface-background);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  perspective: 1000px;
}

.product-image img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  backface-visibility: hidden;
  transform-style: preserve-3d;
}

.product-card:hover .product-image img {
  transform: scale(1.1) rotate(2deg);
  animation: cardImageHover 0.6s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

@keyframes cardImageHover {
  0% { transform: translateZ(0) rotate(0); }
  100% { transform: translateZ(20px) rotate(2deg); }
}

.buy-button {
  position: relative;
  overflow: hidden;
}

.buy-button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 150%;
  height: 150%;
  background: radial-gradient(
    circle,
    rgba(255, 255, 255, 0.2) 0%,
    transparent 70%
  );
  transform: translate(-50%, -50%) scale(0);
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.buy-button:hover::before {
  transform: translate(-50%, -50%) scale(1);
}

.buy-button:active {
  animation: buttonPress 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Loading state */
.product-card.loading {
  pointer-events: none;
}

.product-card.loading .product-image::after {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}

/* Filter animations */
.filter-pills {
  position: relative;
}

.filter-select,
.filter-button {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.filter-select:focus,
.filter-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.filter-button:active {
  transform: translateY(0);
}

/* Search box focus effect */
.search-box:focus-within {
  transform: translateY(-2px);
  box-shadow: 0 0 0 2px var(--accent-primary-alpha),
              var(--shadow-lg);
}

/* Category pill animations */
.category-pill {
  position: relative;
}

.category-pill::after {
  content: '';
  position: absolute;
  inset: -1px;
  background: linear-gradient(135deg, 
    var(--accent-primary), 
    var(--system-purple)
  );
  border-radius: 100px;
  opacity: 0;
  z-index: -1;
  transition: opacity 0.3s ease;
}

.category-pill:hover::after {
  opacity: 0.1;
}

.category-pill.active::after {
  opacity: 1;
}

/* Microinteractions */
@media (hover: hover) {
  .product-card:hover {
    z-index: 1;
  }

  .product-card:hover .product-info {
    transform: translateY(-8px);
  }

  .product-badges span {
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }

  .product-card:hover .product-badges span {
    transform: scale(1.1);
  }
}
</style>
