<template>
  <div class="my-collection">
    <div class="collection-header">
      <h1>My Collection</h1>
      <div class="stats">
        <div class="stat-item">
          <span class="material-symbols-rounded">diamond</span>
          <div class="stat-content">
            <h3>Total Cards</h3>
            <p>{{ collection.length }}</p>
          </div>
        </div>
        <div class="stat-item">
          <span class="material-symbols-rounded">payments</span>
          <div class="stat-content">
            <h3>Total Value</h3>
            <p>${{ totalValue.toFixed(2) }}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="cards-grid" v-if="collection.length">
      <Card
        v-for="card in collection"
        :key="card.id"
        :card="card"
        @card-selected="showCardDetails"
      >
        <button 
          class="remove-button"
          @click.stop="removeFromCollection(card.id)"
        >
          <span class="material-symbols-rounded">delete</span>
        </button>
      </Card>
    </div>

    <div v-else class="empty-state">
      <span class="material-symbols-rounded">category</span>
      <h2>Your collection is empty</h2>
      <p>Start adding cards from the collection page!</p>
      <router-link to="/collection" class="browse-button">
        Browse Cards
      </router-link>
    </div>

    <CardDetailsModal
      v-if="selectedCard"
      :card="selectedCard"
      @close="selectedCard = null"
    />
  </div>
</template>

<script>
import Card from '@/components/Card.vue';
import CardDetailsModal from '@/components/CardDetailsModal.vue';
import collectionsStore from '@/store/collections';

export default {
  components: { Card, CardDetailsModal },
  data() {
    return {
      collection: [],
      selectedCard: null
    };
  },
  computed: {
    totalValue() {
      return this.collection.reduce((sum, card) => {
        const price = parseFloat(card.price) || 0;
        return sum + price;
      }, 0);
    }
  },
  methods: {
    loadCollection() {
      this.collection = collectionsStore.getCollection();
    },
    removeFromCollection(cardId) {
      collectionsStore.removeFromCollection(cardId);
      this.loadCollection();
    },
    showCardDetails(card) {
      this.selectedCard = card;
    }
  },
  mounted() {
    this.loadCollection();
  }
};
</script>

<style scoped>
.my-collection {
  padding: 24px;
}

.collection-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
}

.stats {
  display: flex;
  gap: 24px;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 24px;
  background: var(--card-background);
  border-radius: 12px;
  border: 1px solid var(--card-border);
}

.stat-content h3 {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

.stat-content p {
  font-size: 24px;
  font-weight: 600;
  color: var(--accent-primary);
  margin: 4px 0 0 0;
}

.empty-state {
  text-align: center;
  padding: 48px;
  color: var(--text-secondary);
}

.empty-state .material-symbols-rounded {
  font-size: 48px;
  margin-bottom: 16px;
}

.browse-button {
  display: inline-block;
  margin-top: 24px;
  padding: 12px 24px;
  background: var(--accent-primary);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.browse-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.remove-button {
  position: absolute;
  top: 8px;
  right: 8px;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
  opacity: 0;
  transition: all 0.3s ease;
}

.card:hover .remove-button {
  opacity: 1;
}
</style>
