const COLLECTION_KEY = 'pokemon-collection';
const WISHLIST_KEY = 'pokemon-wishlist';

export default {
  getCollection() {
    return JSON.parse(localStorage.getItem(COLLECTION_KEY) || '[]');
  },

  getWishlist() {
    return JSON.parse(localStorage.getItem(WISHLIST_KEY) || '[]');
  },

  addToCollection(card) {
    const collection = this.getCollection();
    if (!collection.some(item => item.id === card.id)) {
      collection.push(card);
      localStorage.setItem(COLLECTION_KEY, JSON.stringify(collection));
    }
  },

  addToWishlist(card) {
    const wishlist = this.getWishlist();
    if (!wishlist.some(item => item.id === card.id)) {
      wishlist.push(card);
      localStorage.setItem(WISHLIST_KEY, JSON.stringify(wishlist));
    }
  },

  removeFromCollection(cardId) {
    const collection = this.getCollection();
    const updated = collection.filter(card => card.id !== cardId);
    localStorage.setItem(COLLECTION_KEY, JSON.stringify(updated));
  },

  removeFromWishlist(cardId) {
    const wishlist = this.getWishlist();
    const updated = wishlist.filter(card => card.id !== cardId);
    localStorage.setItem(WISHLIST_KEY, JSON.stringify(updated));
  },

  isInCollection(cardId) {
    return this.getCollection().some(card => card.id === cardId);
  },

  isInWishlist(cardId) {
    return this.getWishlist().some(card => card.id === cardId);
  }
};
