<template>
  <div class="modal-backdrop" @click="$emit('close')">
    <div class="modal-content" @click.stop>
      <button class="close-button" @click="$emit('close')">
        <span class="material-symbols-rounded">close</span>
      </button>

      <div class="modal-header">
        <div class="card-preview">
          <img :src="card.imageUrl" :alt="card.name">
        </div>
        <div class="header-info">
          <h1>{{ card.name }}</h1>
          <div class="tags">
            <span class="tag">{{ card.type }}</span>
            <span class="tag rarity">{{ card.rarity }}</span>
          </div>
        </div>
      </div>

      <div class="modal-body">
        <div class="info-grid">
          <div class="info-widget">
            <span class="material-symbols-rounded widget-icon">sell</span>
            <div class="widget-content">
              <h3>Market Price</h3>
              <p class="price">${{ formatPrice(card.price) }}</p>
              <p class="trend up">+5.2% this week</p>
            </div>
          </div>

          <div class="info-widget">
            <span class="material-symbols-rounded widget-icon">bar_chart</span>
            <div class="widget-content">
              <h3>Market Status</h3>
              <p class="status">High Demand</p>
              <div class="mini-chart"></div>
            </div>
          </div>

          <div class="info-widget">
            <span class="material-symbols-rounded widget-icon">collections</span>
            <div class="widget-content">
              <h3>Set Information</h3>
              <p>{{ getSetName }}</p>
              <p class="set-date">Released: {{ getSetReleaseDate }}</p>
            </div>
          </div>
        </div>

        <div class="action-buttons">
          <button 
            class="action-button primary"
            @click="addToCollection"
            :disabled="isInCollection"
          >
            <span class="material-symbols-rounded">
              {{ isInCollection ? 'check' : 'add_shopping_cart' }}
            </span>
            {{ isInCollection ? 'In Collection' : 'Add to Collection' }}
          </button>
          <button 
            class="action-button secondary"
            @click="addToWishlist"
            :disabled="isInWishlist"
          >
            <span class="material-symbols-rounded">
              {{ isInWishlist ? 'favorite' : 'favorite_border' }}
            </span>
            {{ isInWishlist ? 'In Wishlist' : 'Add to Wishlist' }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import collectionsStore from '@/store/collections';

export default {
  name: 'CardDetailsModal',
  props: {
    card: {
      type: Object,
      required: true
    }
  },
  computed: {
    getSetName() {
      return this.card.set && this.card.set.name ? this.card.set.name : 'Unknown Set';
    },
    getSetReleaseDate() {
      return this.formatDate(this.card.set && this.card.set.releaseDate);
    },
    isInCollection() {
      return collectionsStore.isInCollection(this.card.id);
    },
    isInWishlist() {
      return collectionsStore.isInWishlist(this.card.id);
    }
  },
  methods: {
    formatPrice(price) {
      return price === 'N/A' ? 'N/A' : Number(price).toFixed(2);
    },
    formatDate(date) {
      if (!date) return 'Unknown';
      return new Date(date).toLocaleDateString('en-US', {
        month: 'long',
        year: 'numeric'
      });
    },
    addToCollection() {
      collectionsStore.addToCollection(this.card);
      this.$emit('added-to-collection');
    },
    addToWishlist() {
      collectionsStore.addToWishlist(this.card);
      this.$emit('added-to-wishlist');
    }
  }
};
</script>

<style scoped>
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(24px);
  -webkit-backdrop-filter: blur(24px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease;
}

.modal-content {
  background: var(--background-elevated);
  border-radius: 20px;
  width: 90%;
  max-width: 800px;
  position: relative;
  overflow: hidden;
  animation: slideUp 0.4s cubic-bezier(0.16, 1, 0.3, 1);
}

.modal-header {
  background: linear-gradient(135deg, var(--accent-primary), var(--system-purple));
  padding: 40px;
  display: flex;
  gap: 32px;
  align-items: center;
}

.card-preview img {
  width: 220px;
  border-radius: 12px;
  box-shadow: var(--shadow-lg);
  transition: transform 0.3s ease;
}

.card-preview img:hover {
  transform: scale(1.05) rotate(2deg);
}

.header-info {
  color: white;
}

.header-info h1 {
  font-size: 32px;
  font-weight: 700;
  margin: 0 0 16px 0;
  letter-spacing: -0.02em;
}

.tags {
  display: flex;
  gap: 8px;
}

.tag {
  background: rgba(255, 255, 255, 0.2);
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 500;
}

.modal-body {
  padding: 32px;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
  margin-bottom: 32px;
}

.info-widget {
  background: var(--card-background);
  border-radius: 16px;
  padding: 24px;
  display: flex;
  gap: 16px;
  transition: transform 0.3s ease;
}

.info-widget:hover {
  transform: translateY(-4px);
}

.widget-icon {
  font-size: 28px;
  color: var(--accent-primary);
}

.widget-content h3 {
  font-size: 16px;
  font-weight: 600;
  margin: 0 0 8px 0;
  color: var(--text-secondary);
}

.price {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
}

.trend {
  font-size: 14px;
  margin: 4px 0 0 0;
}

.trend.up { color: var(--system-green); }
.trend.down { color: var(--system-red); }

.action-buttons {
  display: flex;
  gap: 16px;
  margin-top: 32px;
}

.action-button {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 16px;
  border-radius: 12px;
  border: none;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.action-button.primary {
  background: var(--accent-primary);
  color: white;
}

.action-button.secondary {
  background: var(--surface-background);
  color: var(--text-primary);
}

.close-button {
  position: absolute;
  top: 16px;
  right: 16px;
  background: rgba(0, 0, 0, 0.3);
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: white;
  transition: all 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { 
    opacity: 0;
    transform: translateY(40px);
  }
  to { 
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .modal-header {
    flex-direction: column;
    text-align: center;
    padding: 24px;
  }

  .info-grid {
    grid-template-columns: 1fr;
  }

  .action-buttons {
    flex-direction: column;
  }
}
</style>
