<template>
  <div class="wishlist">
    <div class="wishlist-header">
      <h1>My Wishlist</h1>
      <div class="stats">
        <div class="stat-item">
          <span class="material-symbols-rounded">favorite</span>
          <div class="stat-content">
            <h3>Wanted Cards</h3>
            <p>{{ wishlist.length }}</p>
          </div>
        </div>
        <div class="stat-item">
          <span class="material-symbols-rounded">savings</span>
          <div class="stat-content">
            <h3>Total Value</h3>
            <p>${{ estimatedTotal.toFixed(2) }}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="cards-grid" v-if="wishlist.length">
      <Card
        v-for="card in wishlist"
        :key="card.id"
        :card="card"
        @card-selected="showCardDetails"
      >
        <div class="card-actions">
          <button 
            class="action-button primary"
            @click.stop="addToCollection(card)"
            v-if="!isInCollection(card.id)"
          >
            <span class="material-symbols-rounded">add_shopping_cart</span>
          </button>
          <button 
            class="action-button danger"
            @click.stop="removeFromWishlist(card.id)"
          >
            <span class="material-symbols-rounded">favorite</span>
          </button>
        </div>
      </Card>
    </div>

    <div v-else class="empty-state">
      <span class="material-symbols-rounded">favorite_border</span>
      <h2>Your wishlist is empty</h2>
      <p>Start adding cards you want to collect!</p>
      <router-link to="/collection" class="browse-button">
        Browse Cards
      </router-link>
    </div>

    <CardDetailsModal
      v-if="selectedCard"
      :card="selectedCard"
      @close="selectedCard = null"
      @added-to-collection="handleAddedToCollection"
    />
  </div>
</template>

<script>
import Card from '@/components/Card.vue';
import CardDetailsModal from '@/components/CardDetailsModal.vue';
import collectionsStore from '@/store/collections';

export default {
  components: { Card, CardDetailsModal },
  data() {
    return {
      wishlist: [],
      selectedCard: null
    };
  },
  computed: {
    estimatedTotal() {
      return this.wishlist.reduce((sum, card) => {
        const price = parseFloat(card.price) || 0;
        return sum + price;
      }, 0);
    }
  },
  methods: {
    loadWishlist() {
      this.wishlist = collectionsStore.getWishlist();
    },
    removeFromWishlist(cardId) {
      collectionsStore.removeFromWishlist(cardId);
      this.loadWishlist();
    },
    addToCollection(card) {
      collectionsStore.addToCollection(card);
      collectionsStore.removeFromWishlist(card.id);
      this.loadWishlist();
    },
    isInCollection(cardId) {
      return collectionsStore.isInCollection(cardId);
    },
    showCardDetails(card) {
      this.selectedCard = card;
    },
    handleAddedToCollection() {
      this.loadWishlist();
    }
  },
  mounted() {
    this.loadWishlist();
  }
};
</script>

<style scoped>
.wishlist {
  padding: 24px;
}

.wishlist-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
}

.stats {
  display: flex;
  gap: 24px;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 24px;
  background: var(--card-background);
  border-radius: 12px;
  border: 1px solid var(--card-border);
}

.stat-content h3 {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

.stat-content p {
  font-size: 24px;
  font-weight: 600;
  color: var(--accent-primary);
  margin: 4px 0 0 0;
}

.cards-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 20px;
  padding: 20px 0;
}

.card-actions {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  gap: 8px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.card:hover .card-actions {
  opacity: 1;
}

.action-button {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  backdrop-filter: blur(8px);
  transition: all 0.3s ease;
}

.action-button.primary {
  background: var(--accent-primary);
  color: white;
}

.action-button.danger {
  background: var(--system-red);
  color: white;
}

.empty-state {
  text-align: center;
  padding: 48px;
  color: var(--text-secondary);
}

.empty-state .material-symbols-rounded {
  font-size: 48px;
  margin-bottom: 16px;
}

.browse-button {
  display: inline-block;
  margin-top: 24px;
  padding: 12px 24px;
  background: var(--accent-primary);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.browse-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

@media (max-width: 1440px) {
  .cards-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 1200px) {
  .cards-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .wishlist-header {
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }
  
  .cards-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .stats {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .cards-grid {
    grid-template-columns: 1fr;
    max-width: 320px;
    margin: 0 auto;
  }
  
  .stats {
    flex-direction: column;
  }
}
</style>
